#modal-sounds{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between; 
	width:100%;
	margin: 7px 0px;
	padding: 4px;
	background-color: rgba(5, 0, 0, 0.4);
}

table:nth-child(1) {
  background-color: rgba(5, 0, 0, 0);
  width: 100%;
}

.modal-edge {
	color: rgba(213, 74, 74, 1);
	background-color: rgba(5, 0, 0, 0.7);
}

.modal-body{
	background-color: rgba(5, 5, 5, 0.3);
	color: white;
	font-style: bold;
}
.wrapper-disco{
	width: 500px;
	height: 500px;
	margin: auto;
}

.bg-carousel{
	margin: auto;
	width: 210px;
	height: 140px;
	position: relative;
	-webkit-perspective: 1000px;
	        perspective: 1000px;
}

#carousel{
	width: 100%;
	height: 100%;
	position: absolute;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-animation: rotation 20s infinite linear;
	        animation: rotation 20s infinite linear;
}
#carousel:hover{
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
}
#carousel figure{
	display: block;
	position: absolute;
	width: 100%;
/*	height: 50%;
*/	left: 0;
	top: 100px;		
}
#carousel figure:nth-child(1){  -webkit-transform: rotateY(0deg)  translateZ(200px);  transform: rotateY(0deg)  translateZ(200px); }
#carousel figure:nth-child(2) { -webkit-transform: rotateY(70deg) translateZ(200px); transform: rotateY(70deg) translateZ(200px);}
#carousel figure:nth-child(3) { -webkit-transform: rotateY(140deg) translateZ(200px); transform: rotateY(140deg) translateZ(200px);}
#carousel figure:nth-child(4) { -webkit-transform: rotateY(210deg) translateZ(200px); transform: rotateY(210deg) translateZ(200px);}
#carousel figure:nth-child(5) { -webkit-transform: rotateY(280deg) translateZ(200px); transform: rotateY(280deg) translateZ(200px);}

.disco-img{
	-webkit-filter: grayscale(1);
	cursor: pointer;
	transition: all .5s ease;
	border-radius: 50%;
	width: 250px;
/*	height: 250px;
*/	border: 1px solid black;
}
.disco-img:hover{
	-webkit-filter: grayscale(0);
}

@-webkit-keyframes rotation{
	from{
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	to{
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}

@keyframes rotation{
	from{
		-webkit-transform: rotateY(0deg);
		        transform: rotateY(0deg);
	}
	to{
		-webkit-transform: rotateY(360deg);
		        transform: rotateY(360deg);
	}
}



